.card {
  background: transparent;
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  height: 80vh;
  width: 80vw;
  overflow: hidden;
  z-index: 999;
}

.container {
  background: inherit;
  background-size: cover;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  -webkit-backdrop-filter: blur(10px);
          backdrop-filter: blur(10px);
}

.select-choice {
  width: 12.5rem;
  height: 12.5rem;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  display: inline-block;
  margin-bottom: 10px;
  border-radius: 10px;  
  position: relative;
}

.select-choice::before {
  content: '';
  position: absolute;
  top: 0; right: 0; bottom: 0; left: 0;
  z-index: -1;
  margin: -2px;
  border-radius: 12px; /* !importanté */
  background: red;
}


.select-choice:hover::before {
  -webkit-animation: spinningGradient 0.5s ease-in-out infinite;
          animation: spinningGradient 0.5s ease-in-out infinite;
}

.select-choice::-moz-selection::before {
  outline: 1px solid black
}

.select-choice::selection::before {
  outline: 1px solid black
}

@-webkit-keyframes spinningGradient {
  0%{ background: -webkit-gradient(linear,left bottom, left top,from(#ffffff), color-stop(cyan), to(gold)); background: linear-gradient(0turn,#ffffff, cyan, gold); }
  10%{ background: linear-gradient(0.05turn,#ffffff, cyan, gold); }
  20%{ background: linear-gradient(0.1turn,#ffffff, cyan, gold); }
  30%{ background: linear-gradient(0.15turn,#ffffff, cyan, gold); }
  40%{ background: linear-gradient(0.2turn,#ffffff, cyan, gold); }
  50%{ background: -webkit-gradient(linear,left top, right top,from(#ffffff), color-stop(cyan), to(gold)); background: linear-gradient(0.25turn,#ffffff, cyan, gold); }
  60%{ background: linear-gradient(0.3turn,#ffffff, cyan, gold); }
  70%{ background: linear-gradient(0.35turn,#ffffff, cyan, gold); }
  80%{ background: linear-gradient(0.4turn,#ffffff, cyan, gold); }
  90%{ background: linear-gradient(0.45turn,#ffffff, cyan, gold); }
  100%{ background: -webkit-gradient(linear,left top, left bottom,from(#ffffff), color-stop(cyan), to(gold)); background: linear-gradient(0.5turn,#ffffff, cyan, gold); }
}

@keyframes spinningGradient {
  0%{ background: -webkit-gradient(linear,left bottom, left top,from(#ffffff), color-stop(cyan), to(gold)); background: linear-gradient(0turn,#ffffff, cyan, gold); }
  10%{ background: linear-gradient(0.05turn,#ffffff, cyan, gold); }
  20%{ background: linear-gradient(0.1turn,#ffffff, cyan, gold); }
  30%{ background: linear-gradient(0.15turn,#ffffff, cyan, gold); }
  40%{ background: linear-gradient(0.2turn,#ffffff, cyan, gold); }
  50%{ background: -webkit-gradient(linear,left top, right top,from(#ffffff), color-stop(cyan), to(gold)); background: linear-gradient(0.25turn,#ffffff, cyan, gold); }
  60%{ background: linear-gradient(0.3turn,#ffffff, cyan, gold); }
  70%{ background: linear-gradient(0.35turn,#ffffff, cyan, gold); }
  80%{ background: linear-gradient(0.4turn,#ffffff, cyan, gold); }
  90%{ background: linear-gradient(0.45turn,#ffffff, cyan, gold); }
  100%{ background: -webkit-gradient(linear,left top, left bottom,from(#ffffff), color-stop(cyan), to(gold)); background: linear-gradient(0.5turn,#ffffff, cyan, gold); }
}

@-webkit-keyframes spinningBorderGradient {
  0%{ outline-color: linear-gradient(0turn,#ffffff, cyan, gold); }
