  90%{ outline-color: linear-gradient(0.45turn,#ffffff, cyan, gold); }
  100%{ outline-color: linear-gradient(0.5turn,#ffffff, cyan, gold); }
}

@keyframes spinningBorderGradient {
  0%{ outline-color: linear-gradient(0turn,#ffffff, cyan, gold); }
