            transform: translateY(-50%);
    text-align: center;
    margin-left: 150px;
    color: white;
    font-family: 'Press Start 2P', cursive;
    font-size: 200%;
    -webkit-animation: intro 100s ease-out infinite;
            animation: intro 100s ease-out infinite;
    z-index: 1;
}

