}

#logo {
    z-index: 1;
    opacity: 0;
    position: absolute;
    width: 80%;
    left: 50%;
    height: 60%;
    top: 70%;
    -webkit-transform: translateX(-50%) translateY(-50%);
            transform: translateX(-50%) translateY(-50%);
    -webkit-animation: logo 100s ease-out;
            animation: logo 100s ease-out;
    /* background-image: url(../images/FireLogo.jpg); */
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center center;
    border-radius: 80%;
}

#titles {
    position: absolute;
    width: 18em;
    height: 50em;
    bottom: 0;
    left: 50%;
    margin-left: -9em;
    font-size: 250%;
    font-weight: bold;
    text-align: justify;
    overflow: hidden;
    -webkit-transform-origin: 50% 100%;
            transform-origin: 50% 100%;
    -webkit-transform: perspective(18.75rem) rotateX(25deg);
            transform: perspective(18.75rem) rotateX(25deg);
}

#titles:after {
    position: absolute;
    content: ' ';
    left: 0;
    right: 0;
    top: 0;
    bottom: 60%;
    background-image: -webkit-gradient(linear, left top, left bottom, from(rgba(0, 0, 0, 1)), to(transparent));
    background-image: linear-gradient(top, rgba(0, 0, 0, 1) 0%, transparent 100%);
    pointer-events: none;
    font-family: 'Press Start 2P', cursive;
}

#titlecontent {
    margin-left: 1.25rem;
    position: absolute;
    top: 100%;
    overflow: hidden;
    -webkit-animation: scroll 150s linear 4s;
            animation: scroll 150s linear 4s;
    -webkit-animation-delay: 15s;
            animation-delay: 15s;
    color: white;
    font-family: 'Press Start 2P', cursive;
}

@-webkit-keyframes scroll {
    0% {
        top: 100%;
    }
    100% {
        top: -170%;
    }
}

@keyframes scroll {
    0% {
        top: 100%;
    }
    100% {
        top: -170%;
    }
}

@-webkit-keyframes intro {
    0% {
        opacity: 0;
    }
    1% {
        opacity: 0;
    }
    1.5% {
        opacity: 1;
    }
    5.5% {
        opacity: 1;
    }
    6% {
        opacity: 0;
    }
}

@keyframes intro {
    0% {
        opacity: 0;
    }
    1% {
        opacity: 0;
    }
    1.5% {
        opacity: 1;
    }
    5.5% {
        opacity: 1;
    }
    6% {
        opacity: 0;
    }
}

@-webkit-keyframes logo {
    9% {
        -webkit-transform: translateX(-50%) translateY(-50%) scale(2);
                transform: translateX(-50%) translateY(-50%) scale(2);
        opacity: 0;
    }
    9.1% {
        opacity: 1;
    }
    14% {
        opacity: 1;
    }
    18% {
        -webkit-transform: translateX(-50%) translateY(-50%) scale(0.1);
                transform: translateX(-50%) translateY(-50%) scale(0.1);
        opacity: 0;
    }
}

@keyframes logo {
    9% {
        -webkit-transform: translateX(-50%) translateY(-50%) scale(2);
                transform: translateX(-50%) translateY(-50%) scale(2);
        opacity: 0;
    }
    9.1% {
        opacity: 1;
    }
    14% {
        opacity: 1;
    }
    18% {
        -webkit-transform: translateX(-50%) translateY(-50%) scale(0.1);
                transform: translateX(-50%) translateY(-50%) scale(0.1);
        opacity: 0;
    }
}
